<template>
  <div>
    <!--<top-title title="概况"></top-title>-->

    <div class="topTitle-wrap">
      <div>
        <span class="tpTitle">概况</span>
      </div>
      <div class="" style="margin-right: 20px">
        <ul class="myDatePicker" slot="mySolt">
          <li class="myDatePicker_li">
            <span>日期范围</span>
            <DatePicker @on-change="startTime" :value="dateRange" type="daterange" placeholder=""
                        placement="bottom-end"
                        :clearable=false :editable=false
                        style="width: 200px;"></DatePicker>
          </li>
        </ul>
      </div>
    </div>

    <div style="background-color:#f7f7f7;padding: 10px;margin: 5px;font-size: 14px">
      <Row>
        <Col span="5">
          <Icon type="android-volume-up"></Icon>
          <router-link style="text-decoration:underline" :to="{ name: 'CodeManagement', params: { flag: 1 }}">
            {{codeCount}}
          </router-link>
          <span>个代码位即将在3天内空闲</span>
        </Col>
        <Col span="5">
          <router-link style="text-decoration:underline" :to="{ name: 'advertisingManagement', params: { flag: 1 }}">
            {{countDeliver}}
          </router-link>
          <span>个广告投放即将在3天内空闲</span>
        </Col>
      </Row>

    </div>

    <div class="fu">
      <div>
        <ul class="top-wrap">
          <li>
            <p>昨日广告展现量</p>
            <p class="count">{{yesterdayCount}}</p>
          </li>
          <li>
            <p>过去7天广告展现量</p>
            <p class="count">{{day7Count}}</p>
          </li>
          <li>
            <p>过去30天广告展现量</p>
            <p class="count">{{day30Count}}</p>
          </li>
        </ul>
      </div>
      <!--<div class="DatePicker-wrap">-->
      <!--<span class="selectStartEndDate">选择开始与结束时间：</span>-->
      <!--<DatePicker class="myDatePicker" type="daterange" @on-change="startTime" :value="datesValue" :options="optionsDisable" split-panels placeholder="Select date" style="width: 200px"></DatePicker>-->
      <!--</div>-->
      <div id="main" style="" v-show="isShowChart">

      </div>

      <div v-show="isDiv" class="noData" style="font-size: 22px">
        <span>暂无数据</span>
      </div>

      <div class="ract-wrap">
        <div @click="showNum">
          <span class="showRact"></span>
          <span>展现量</span>

        </div>
        <div class="ract-wrap" @click="clickNum">
          <span class="clickRact"></span>
          <span>点击量</span>

        </div>
      </div>
    </div>

  </div>

</template>
<style scoped>

  .noData {

    display: flex;
    display: -webkit-flex; /* Safari */
    justify-content: center;
    height: 400px;
    align-items: center;
  }

  .top-wrap {
    display: flex;
    justify-content: space-around;
  }

  .DatePicker-wrap {
    display: flex;
    justify-content: flex-start;
    margin-top: 50px;
    margin-left: 100px;
  }

  .selectStartEndDate, .myDatePicker {
    display: inline-block;
    vertical-align: middle;
    line-height: 32px;
  }

  .top-wrap li {
    text-align: center;
    /*border: 1px solid #d7f0f2;*/
    padding: 10px;
    color: #c5c5c5;
  }

  .count {
    font: 22px '微软雅黑', Arial, '宋体', Helvetica, sans-serif;
    color: #555;
  }

  #main {
    width: 90%;
    height: 400px;
  }

  .showRact, .clickRact {
    display: inline-block;
    width: 30px;
    height: 15px;
  }

  .showRact {
    background: #044b7c;
  }

  .clickRact {
    background: #ea1107;
  }

  .ract-wrap {
    text-align: center;
  }

  .ract-wrap div {
    display: inline-block;

  }

  .myDatePicker, .myDatePicker_li {
    display: inline-block;
    color: black;
  }

  .myDatePicker {

  }

  .myDatePicker_li {

  }

  .ract-wrap div span {
    display: inline-block;
    vertical-align: middle;
  }

  .ract-wrap div:nth-child(1) {
    margin-right: 100px;
  }
</style>
<script>
  import topTitle from '@/components/common/topTitle'
  import echarts from 'echarts'

  export default {
    components: {topTitle},
    data() {
      return {
        day7Count: '',
        day30Count: '',
        yesterdayCount: '',
        startDay: '',
        endDay: '',
        dateRange: [],
        startDayValue: '',
        endDayValue: '',
        codeCount: '--',
        countDeliver: '--',
        pageSize: 10,
        pageNo: 1,
        isShowChart: true,
        isDiv: false,
        dataClickNum: [],
        dataShowNum: [],
        dataAdDateDay: [],
        myChart: '',
        chartArr: [],
        chartArrClick: [],
        chartColor: '#d6eaff',
        bolChart: false,
        datesValue: [],
        optionsDisable: {
          // disabledDate (date) {
          //   let initdate = new Date() - 86400000*30;//前一个月时间
          //   return (date && date.valueOf() < initdate) || (date && date.valueOf() >= new Date())
          // }
        },


      }
    },
    created() {
      this.getWillEndCodeCount();
      this.getBasicReport();
    },
    mounted() {
      this.startDay = this.getPreDate(-8);//获取一周的时间
      this.endDay = this.getPreDate(-1);
      this.dateRange = [this.startDay, this.endDay]
      this.myChart = echarts.init(document.getElementById('main'));

      this.getOverallReportChart();
      // setTimeout(() =>{
      //   this.getOverallReportChart();
      // },500);


    },
    methods: {
      initEcharts() {

        var option = {
          title: {
            text: ''
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              label: {
                backgroundColor: '#6a7985'
              }
            }
          },
          xAxis: {
            type: 'category',
            boundaryGap: false,
            data: this.dataAdDateDay,
          },
          yAxis: [
            // type: 'value'
            {
              name: '展现量',
              type: 'value'
            },
            {
              name: '点击量',
              type: 'value',

            }
          ],
          minInterval: 1,
          color: ["#044b7c", "#ea1107"],

          series: [
            {
              name: '展现量',
              data: this.dataShowNum,
              type: 'line',
              areaStyle: {
                color: '#d6eaff',
              },
              lineStyle: {
                color: '#044b7c',
              },

            },
            {
              name: '点击量',
              data: this.dataClickNum,
              yAxisIndex: 1,
              type: 'line',
              areaStyle: {
                color: '#ffd6e1',
              },
              lineStyle: {
                color: '#ea1107',
              },


            }
          ]
        };

        // 使用刚指定的配置项和数据显示图表。
        this.myChart.setOption(option);
      },

      getPreDate(days) {
        let date = new Date();//获取当前时间
        date.setDate(date.getDate() + days);//设置天数 -1 天
        return date.Format("yyyy-MM-dd");
      },
      splitNum(num) {
        var reg = /(\d)(?=(?:\d{3})+$)/g;
        var dec = (num + '').split('.');
        if (dec.length == 2) {
          return dec[0].replace(reg, '$1,') + '.' + dec[1];
        } else {
          return dec[0].replace(reg, '$1,');
        }
      },
      getBasicReport() {
        const url = `/report/getBasicReport.json?`;
        this.$Http.get(url, {}).then(res => {
          console.log(res.body)
          if (res.body.status == 0) {
            let dayCount = res.body.data.result;
            this.yesterdayCount = this.splitNum(dayCount.yesterdayCount);
            this.day7Count = this.splitNum(dayCount.day7Count);
            this.day30Count = this.splitNum(dayCount.day30Count);
          }

        });
      },
      getWillEndCodeCount() {
        const url = `/common/getWillEndCodeCount.json?`;
        this.$Http.get(url, {}).then(res => {
          console.log(res.body)
          if (res.body.status == 0) {
            let data = res.body.data;
            this.codeCount = data.count;
            this.countDeliver = data.countDeliver;
          }

        });
      },
      getOverallReportChart() {
        const url = `/report/getOverallReportChart.json?startDay=${this.startDay}&endDay=${this.endDay}`;
        this.$Http.get(url, {}).then(res => {
          console.log(res)
          if (res.body.status == 0) {
            let chartList = res.body.data.result;
            let clickNumArr = [];
            let showNumArr = [];
            this.dataAdDateDay = [];
            chartList.forEach((item, index) => {
              clickNumArr.push(item.clickNum);
              showNumArr.push(item.showNum);
              this.dataAdDateDay.push(item.adDateDay)
            })
            this.dataClickNum = clickNumArr;
            this.dataShowNum = showNumArr;

            this.chartArr = this.dataShowNum;
            this.chartArrClick = this.dataClickNum;


            // if (this.bolChart == false) {
            //     this.chartArr = this.dataShowNum
            //     this.chartColor = "#d6eaff";
            // }else{
            //     this.chartArr = this.dataClickNum
            //     this.chartColor = "#ffd6e1";
            // }
            // this.dataAdDateDay.reverse()
            this.myChart.clear();
            this.initEcharts();

            // if(this.dataClickNum.length >0 ||  this.dataShowNum.length>0){
            //    this.isShowChart =true;
            //   this.isDiv =false;
            // }else {
            //   this.isDiv =true;
            //   this.isShowChart =false;
            // }
          }
        });
      },

      initDataChart() {

      },
      startTime(data) {
        if (data.length < 2 || data[0] == '' || data[1] == '') {
          this.$Message.error('请选择日期范围');
          return false;
        }
        this.startDay = data[0];
        this.endDay = data[1];
        this.pageNo = 1;
        this.getOverallReportChart();
      },


      showNum() {
        // this.bolChart = false;
        // this.dataShowNum = this.chartArr;
        // this.dataClickNum=[];
        // this.myChart.clear();
        // this.chartColor = "#d6eaff";
        // this.initEcharts();
      },
      clickNum() {
        // this.bolChart = true;
        // this.dataClickNum = this.chartArrClick;
        // this.dataShowNum=[];
        // this.myChart.clear();
        // this.chartColor = "#ffd6e1";
        // this.initEcharts();
      }
    }
  }
</script>



